import * as actionType from "../actions";

const initialState = {
  persons: [],
};

const personReducer = (state = initialState, action) => {
  switch (action.type) {
    case actionType.ADD_PERSON:
      return {
        ...state,
        persons: state.persons.concat(),
      };

    default:
      break;
  }
  return state;
};

export default personReducer;



